import React from 'react'
import { useNavigate } from 'react-router-dom'
import oopsImg from './oops.png'

import './style.css'

const Oops = ({ type }) => {
  const navigate = useNavigate()
  const returnToHome = () => {
    navigate('/')
  }
  return (
    <div className="oops-container">
      <img src={oopsImg} alt="oops" />
      {type === 'noSupport' && <p>:)</p>}
      {type === 'noMatch' && (
        <>
          <p>:)</p>
          <p onClick={returnToHome} style={{"cursor": 'pointer', color:'#102233'}}> </p>
        </>
      )}
    </div>
  )
}

export default Oops
